<template>
	<div id="paybill-detail">
		<public-header title="账单详情"></public-header>
		<div class="content">
			<div class="paybill-item">
				<div class="paybill-title flex-lr border-1px-b">
					<div class="member-pay">顾客支付金额</div>
					<div class="price">¥{{order.final_fee}}</div>
				</div>
				<div class="paybill-info">
					<div class="flex-lr paybill-text">
						<div>订单号</div>
						<div>{{order.order_sn}}</div>
					</div>
					<div class="flex-lr paybill-text">
						<div>支付方式</div>
						<div>{{order.pay_type_cn}}</div>
					</div>
					<div class="flex-lr paybill-text">
						<div>消费总额</div>
						<div>￥{{order.total_fee}}</div>
					</div>
					<div class="flex-lr paybill-text">
						<div>不参与优惠金额</div>
						<div>￥{{order.no_discount_part > 0 ? order.no_discount_part : '0.00'}}</div>
					</div>
					<div class="flex-lr paybill-text">
						<div>优惠金额</div>
						<div>￥{{order.discount_fee > 0 ? order.discount_fee : '0.00'}}</div>
					</div>
					<div class="flex-lr paybill-text">
						<div>平台抽取佣金</div>
						<div>￥{{order.plateform_serve_fee}}</div>
					</div>
					<div class="flex-lr paybill-text">
						<div>商户预计收入</div>
						<div>￥{{order.store_final_fee}}</div>
					</div>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				showPreLoading: true,
				order: {
					final_fee: 0,
					order_sn: '',
					pay_type_cn: '',
					total_fee: '',
					no_discount_part: '',
					discount_fee: '',
					plateform_serve_fee: '',
					store_final_fee: ''
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					data: {
						id: that.id
					},
					autoAssign: true,
					variable: 'order',
					url: 'manage/paybill/index/detail',
				});
			},
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#paybill-detail .content .paybill-item{
		background: #fff;
	}
	#paybill-detail .content .paybill-title{
		padding: 10px;
		line-height: 50px;
	}
	#paybill-detail .content .paybill-title .member-pay{
		font-size:17px;
		color:#999;
	}
	#paybill-detail .content .paybill-title .price{
		font-style:normal;
		font-size:26px;
		color:#3d4145;
	}
	#paybill-detail .content .paybill-item .paybill-info{
		padding: 10px;
	}
	#paybill-detail .content .paybill-item .paybill-info .paybill-text{
		font-size: 14px;
		color: #7a7a7a;
		padding-bottom: 10px;
	}
	#paybill-detail .content .paybill-item .paybill-info .paybill-text:last-child{
		padding-bottom: 0px;
	}
</style>


